body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
  box-sizing: border-box;
  text-align: center;
}
body{
  background-image:url(/react-todo-app/static/media/bg.edfd6108.jpg);
  background-size: contain;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
}
.App{
  margin-top: 100px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.login{
  width: 50%;
}
.login form{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}
.login form input{
  color: lime;
  background-color: white;
  height: 30px;
  width: 40%;
  border: 1px solid black;
  font-weight: bold;
  font-size: 18px;
  margin-top: 5px;
}
.login form input:last-child:hover{
  color: white;
  background-color: lime;
}
.text-field{
  background-color: white;
  height: 30px;
  width: 80%;
  border: 2px solid black;
  font-weight: bold;
  font-size: 18px;
  margin-top: 5px;
}
.form-btn{
  color: lime;
  background-color: white;
  height: 30px;
  width: 20%;
  border: 2px solid black;
  font-weight: bold;
  font-size: 18px;
  margin-top: 5px;
}
.form-btn:hover{
  background-color: lime;
  color: white;
}
.item{
  width: 100%;
  margin-top: 5px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  height: 40px;
  font-weight: bold;
  font-size: 18px;
}
.task{
  padding: 10px;
  font-family: 'Merienda One', cursive;
  font-weight: normal;
  font-size: 25px;
}
.btn-del,.btn-edit{
  height: 36px;
  background-color: red;
  color: white;
  font-size: 18px;
  font-weight: bold;
  border: 2px solid black;
}
.btn-edit{
  background-color: lightseagreen ;
  margin-right: 5px;
}
.clearAll{
  background-color: transparent;
  color: red;
  border:1px solid black;
  width: 100px;
  height: 30px;
  font-size: 18px;
  font-weight: bold;
  margin-top: 20px;
}
.clearAll:hover{
  background-color: red;
  color: white;

}
.logout{
  background-color: transparent;
  color: red;
  border:1px solid black;
  width: 100px;
  height: 30px;
  font-size: 18px;
  font-weight: bold;
  margin-top: 20px;
}
.logout:hover
{
  background-color: red;
  color: white;
}
